<template>
    <div style="margin-top: 20px;margin-left: 30px;margin-right: 30px">
      <div class="d-sm-flex align-items-center justify-content-between mb-4">
        <h1 class="h3 mb-0 text-gray-800">AI审核</h1>
      </div>
      <div class="card shadow mb-4 col-lg-12">
        <div class="card-header py-3">
          <h6 class="m-0 font-weight-bold text-primary">AI问询消息列表</h6>
        </div>
        <div class="card-body">
          <div class="table-responsive">
            <table class="table table-bordered" id="dataTable" width="100%" cellspacing="0">
              <thead>
              <tr>
                <!-- <th>
                  <div class="row">
                    <div class="col mr-2">姓名</div>
                    <div class="sort" @click="sort(0,'id')" ref="id">
                      <span class="sorted">↑</span>
                      <span class="sorted_reverse">↓</span>
                    </div>
                  </div>
                </th> -->
                <th>
                  <div class="row">
                    <div class="col mr-2">被照顾对象问题</div>
                    <div class="sort" @click="sort(0,'question')" ref="question">
                      <span class="sorted ">↑</span>
                      <span class="sorted_reverse">↓</span>
                    </div>
                  </div>
                </th>
                <th class="sorted">
                  <div class="row">
                    <div class="col mr-2">AI助手回答</div>
                    <div class="sort" @click="sort(1,'answer')" ref="answer">
                      <span class="sorted ">↑</span>
                      <span class="sorted_reverse">↓</span>
                    </div>
                  </div>
                </th>
                <th class="sorted">
                  <div class="row">
                    <div class="col mr-1">日期</div>
                    <div class="sort" @click="sort(2,'date')" ref="date">
                      <span class="sorted ">↑</span>
                      <span class="sorted_reverse">↓</span>
                    </div>
                  </div>
                </th>
                <th class="sorted">
                  <div class="row">
                    <div class="col mr-1">时间</div>
                    <div class="sort" @click="sort(3,'time')" ref="time">
                      <span class="sorted">↑</span>
                      <span class="sorted_reverse">↓</span>
                    </div>
                  </div>
                </th>
                <th class="sorted">
                  <div class="row">
                    <div class="col mr-1">处理</div>
                    <div class="sort" @click="sort(4,'isHandled')" ref="isHandled">
                      <span class="sorted">↑</span>
                      <span class="sorted_reverse">↓</span>
                    </div>
                  </div>
                </th>
              </tr>
              </thead>
              <tbody>
              <tr v-for="item in this.result.answerData" :key="item.id">
                <td class="col-lg-2">{{ item.question }}</td>
                <td class="col-lg-2">{{ item.answer }}</td>
                <td class="col-lg-1">{{ item.date }} </td>
                <td class="col-lg-1">{{ item.time }}</td>
                <td align="center" class="col-lg-1">
                  <a @click="handleAnswer(item)" :class="['btn', 'btn-icon-split',
                  {
                    'btn-success' : item.isHandled,
                    'btn-danger' : !item.isHandled
                  }]">
                    <span class="icon text-white-50">
                        <i class="fas fa-check"></i>
                    </span>
                    <span class="text">{{ item.isHandled ? '已处理' : '处理' }}</span>
                  </a>
                </td>
              </tr>
              </tbody>
            </table>
          </div>
        </div>
  
      </div>
    </div>
</template>
  
<script>
import axios from "axios";
export default {
  name: 'Review',
  data(){
    return{
      result: {
        answerData: [],
        sortedAnswerData: [],
        sortedStatus: [true,true,true,true,true]
      }
    }
  },
  methods:{
    handleUserIdChange(){
      this.$http.get(`${this.$app.url}/getAnswer?olderId=${this.$app.userId}`).then(
        response => {
          this.result.answerData = response.data
        },
        err => {
          console.log(err)
        }
      );
    },
    sort(index, type){
      if(this.result.sortedStatus[index]){
        this.result.sortedAnswerData = this.result.answerData.sort((a, b) => {
          return a[type] > b[type] ? 1 : -1;
        })
      }else {
        this.result.sortedAnswerData = this.result.answerData.sort((a, b) => {
          return b[type] > a[type] ? 1 : -1;
        })
      }

      this.result.sortedStatus[index] = !this.result.sortedStatus[index]

      const activeElements =  document.getElementsByClassName('active')
      for(let i = 0; i < activeElements.length; i++) {
        activeElements[i].classList.remove("active")
      }

      if(!this.result.sortedStatus[index]){
        this.$refs[type].querySelector(":first-child").classList.add("active")
      }else {
        this.$refs[type].querySelector(":last-child").classList.add("active")
      }
    },
    handleAnswer(item){
      item.isHandled = !item.isHandled
      axios.post(`${this.$app.url}/handleAnswer`, item)
          .then(response =>{
          })
          .catch(error => {
            console.log(error)
          })
    }
  }
  ,
  mounted(){
    this.$watch('$app.userId', this.handleUserIdChange);
    this.$http.get(`${this.$app.url}/getAnswer?olderId=${this.$app.userId}`).then(
        response => {
          this.result.answerData = response.data
        },
        err => {
          console.log(err)
        }
    );
  }
}
</script>
  
<style scoped>
  span.sorted{
  opacity: 0.3;
  user-select: none;
}

span.sorted_reverse{
  margin-right: 10px;
  opacity: 0.3;
  user-select: none;
}

span.active{
  opacity: 1;
}

div.sort{
  cursor: pointer;
}
</style>
  