<template>
    <div id="container" class="container-fluid"></div>
</template>

<script>
export default{
name: 'MapPage',
methods:{
    loadAMap(){
        const script = document.createElement("script");
        script.src = "https://webapi.amap.com/loader.js";
        script.onload = () => {
            this.initAMap();
        };
        document.head.appendChild(script);
    },
    initAMap() {
        window._AMapSecurityConfig = {
            securityJsCode: "1cb204847dd3fb2f9bd9e9592f58fdb1",
        };

        window.AMapLoader.load({
            key: "1cb204847dd3fb2f9bd9e9592f58fdb1",
            version: "2.0",
            })
            .then((AMap) => {
                const map = new AMap.Map("container",{
                    mapStyle: "amap://styles/fresh",
                    viewMode: "2D",                 
                });
                
                const circleMarker = new AMap.CircleMarker({
                    center: [116.326936,40.003213], // 圆心坐标 [经度, 纬度]
                    radius: 10, // 半径，单位米
                    strokeColor: "#F33", // 描边颜色
                    strokeOpacity: 1, // 描边透明度
                    strokeWeight: 0, // 描边宽度
                    fillColor: "#0000ff", // 填充颜色
                    fillOpacity: 0.5, // 填充透明度
                    zIndex: 10, // 层级
                });

                // 将圆形标记添加到地图上
                circleMarker.setMap(map);
            })
            .catch((e) => {
                console.error(e);
            });

    },
},
mounted(){
    this.loadAMap();
}
}
</script>

<style scoped>
#container {
    width: 100%;
    height: 100vh; /* 设置高度为100%屏幕高度 */
}
</style>