import VueRouter from "vue-router";
import register from "@/pages/Register.vue";
import login from "@/pages/Login.vue";
import section from "@/components/Section.vue";
import forgotPassword from "@/pages/ForgotPassword.vue";
import home from "@/pages/Home.vue";
import monitor from "@/pages/Monitor.vue";
import mainBody from "@/components/MainBody.vue";
import today from "@/pages/Today.vue";
import history from "@/pages/History.vue";
import medicine from "@/pages/Medicine.vue";
import TodayE from "@/pages/TodayE.vue";
import HistoryE from "@/pages/HistoryE.vue";
import DoctorInfo from "@/pages/DoctorInfo.vue";
import MapPage from "@/pages/MapPage.vue"
import HRVPage from "@/pages/HRVPage.vue"
import Review from "@/pages/Review.vue";

export default new VueRouter({
    routes:[
        {
          name: 'login',
          path: '/',
          redirect: '/login'
        },
        {
            name: 'login',
            path: '/login',
            component: login,
        },
        {
            name: 'register',
            path: '/register',
            component: register
        },
        {
            name: 'forgot-password',
            path: '/forgot-password',
            component: forgotPassword
        },
        {
            name: 'mapPage',
            path: '/mapPage',
            component: MapPage
        },
        {
            name: 'HRVPage',
            path: '/HRVPage',
            component: HRVPage
        },
        {
            name: 'section',
            path: '/section',
            component: section,
            redirect: '/section/mainbody',
            meta: { requiresAuth: true }, // 需要登录才能访问
            children: [
                {
                    name: 'mainbody',
                    path: 'mainbody',
                    component: mainBody,
                    redirect: '/section/mainbody/home',
                    meta: { requiresAuth: true },
                    children: [
                        {
                            name: 'home',
                            path: 'home',
                            component: home,
                            meta: { requiresAuth: true }
                        },
                        {
                            name: 'monitor',
                            path: 'monitor',
                            component: monitor,
                            meta: { requiresAuth: true }
                        },
                        {
                            name: 'today',
                            path: 'today',
                            component: today,
                            meta: { requiresAuth: true }
                        },
                        {
                            name: 'history',
                            path: 'history',
                            component: history,
                            meta: { requiresAuth: true }
                        },
                        {
                            name: 'todayE',
                            path: 'todayE',
                            component: TodayE,
                            meta: { requiresAuth: true }
                        },
                        {
                            name: 'historyE',
                            path: 'historyE',
                            component: HistoryE,
                            meta: { requiresAuth: true }
                        },
                        {
                            name: 'medicine',
                            path: 'medicine',
                            component: medicine,
                            meta: { requiresAuth: true }
                        },
                        {
                            name: 'review',
                            path: 'review',
                            component: Review,
                            meta: { requiresAuth: true }
                        },
                        {
                            name: 'settings',
                            path: 'settings',
                            component: DoctorInfo,
                            meta: { requiresAuth: true }
                        }
                    ]
                }
            ]
        }
    ],
    // mode: 'history'
})
