<template>
  <div style="margin-top: 20px;margin-left: 30px;margin-right: 30px">
    <div class="d-sm-flex align-items-center justify-content-between mb-4">
      <h1 class="h3 mb-0 text-gray-800">近期用药情况</h1>
    </div>
    <div class="card shadow mb-4 col-lg-12">
      <div class="card-header py-3">
        <h6 class="m-0 font-weight-bold text-primary">用药清单</h6>
      </div>
      <div class="card-body">
        <div class="table-responsive">
          <table class="table table-bordered" id="dataTable" width="100%" cellspacing="0">
            <thead>
            <tr>
              <!-- <th>
                <div class="row">
                  <div class="col mr-2">姓名</div>
                  <div class="sort" @click="sort(0,'id')" ref="id">
                    <span class="sorted">↑</span>
                    <span class="sorted_reverse">↓</span>
                  </div>
                </div>
              </th> -->
              <th>
                <div class="row">
                  <div class="col mr-2">用药名称</div>
                  <div class="sort" @click="sort(0,'medicineName')" ref="medicineName">
                    <span class="sorted ">↑</span>
                    <span class="sorted_reverse">↓</span>
                  </div>
                </div>
              </th>
              <th class="sorted">
                <div class="row">
                  <div class="col mr-1">开始时间</div>
                  <div class="sort" @click="sort(1,'beginDate')" ref="beginDate">
                    <span class="sorted ">↑</span>
                    <span class="sorted_reverse">↓</span>
                  </div>
                </div>
              </th>
              <th class="sorted">
                <div class="row">
                  <div class="col mr-1">持续时间</div>
                  <div class="sort" @click="sort(2,'duration')" ref="duration">
                    <span class="sorted ">↑</span>
                    <span class="sorted_reverse">↓</span>
                  </div>
                </div>
              </th>
              <th class="sorted">
                <div class="row">
                  <div class="col mr-1">每次用量</div>
                  <div class="sort" @click="sort(3,'perDose')" ref="perDose">
                    <span class="sorted">↑</span>
                    <span class="sorted_reverse">↓</span>
                  </div>
                </div>
              </th>
              <th class="sorted">
                <div class="row">
                  <div class="col mr-1">每天用量</div>
                  <div class="sort" @click="sort(4,'everydayTimes')" ref="everydayTimes">
                    <span class="sorted">↑</span>
                    <span class="sorted_reverse">↓</span>
                  </div>
                </div>
              </th>
              <th class="sorted">
                <div class="row">
                  <div class="col mr-2">已服用天数</div>
                  <div class="sort" @click="sort(5,'completedDay')" ref="completedDay">
                    <span class="sorted">↑</span>
                    <span class="sorted_reverse">↓</span>
                  </div>
                </div>
              </th>
              <th class="sorted">
                <div class="row">
                  <div class="col mr-3">备注</div>
                  <div class="sort" @click="sort(6,'comment')" ref="comment">
                    <span class="sorted">↑</span>
                    <span class="sorted_reverse">↓</span>
                  </div>
                </div>
              </th>
              <th class="sorted">
                <div class="row">
                  <div class="col mr-2">处理</div>
                  <div class="sort" @click="sort(6,'comment')" ref="comment">
                    <span class="sorted">↑</span>
                    <span class="sorted_reverse">↓</span>
                  </div>
                </div>
              </th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="item in this.result.medicineData" :key="item.id">
              <td class="col-lg-2">{{ item.medicineName }}</td>
              <td class="col-lg-1">{{ item.beginDate }}</td>
              <td class="col-lg-1">{{ item.duration }} 天</td>
              <td class="col-lg-1">{{ item.perDose }} 粒/袋/片</td>
              <td class="col-lg-1">{{ item.everydayTimes }} 次/天</td>
                <td class="col-lg-2">{{ item.completedDay }} / {{ item.duration }} 天
                <div class="progress">
                  <div class="progress-bar bg-gradient-right-primary" role="progressbar" :style="{width: item.completedDay / item.duration * 100 + '%'}"
                      aria-valuemin="0" aria-valuemax="100"></div>
                </div>
              </td>
              <td class="col-lg-3">{{ item.comment }}</td>
              <td align="center" class="col-lg-2">
                <a @click="handleMedicine(item)" :class="['btn', 'btn-icon-split',
                {
                  'btn-success' : item.isHandled,
                  'btn-danger' : !item.isHandled
                }]">
                  <span class="icon text-white-50">
                      <i class="fas fa-check"></i>
                  </span>
                  <span class="text">{{ item.isHandled ? '已处理' : '处理' }}</span>
                </a>
              </td>
            </tr>
            </tbody>
          </table>
        </div>
      </div>

    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: 'Medicine',
  data(){
    return{
      result: {
        medicineData: [],
        sortedMedicineData: [],
        sortedStatus: [true,true,true,true,true,true,true]
      }
    }
  },
  methods:{
    handleUserIdChange(){
      this.$http.get(`${this.$app.url}/get_medicine?olderId=${this.$app.userId}`).then(
        response => {
          this.result.medicineData = response.data
          console.log(response.data)
        },
        err => {
          console.log(err)
        }
      );
    },
    sort(index, type){
      if(this.result.sortedStatus[index]){
        this.result.sortedMedicineData = this.result.medicineData.sort((a, b) => {
          return a[type] > b[type] ? 1 : -1;
        })
      }else {
        this.result.sortedMedicineData = this.result.medicineData.sort((a, b) => {
          return b[type] > a[type] ? 1 : -1;
        })
      }

      this.result.sortedStatus[index] = !this.result.sortedStatus[index]

      const activeElements =  document.getElementsByClassName('active')
      for(let i = 0; i < activeElements.length; i++) {
        activeElements[i].classList.remove("active")
      }

      if(!this.result.sortedStatus[index]){
        this.$refs[type].querySelector(":first-child").classList.add("active")
      }else {
        this.$refs[type].querySelector(":last-child").classList.add("active")
      }
    },
    handleMedicine(item){
      item.isHandled = !item.isHandled
      axios.post(`${this.$app.url}/handleMedicine`, item)
          .then(response =>{
          })
          .catch(error => {
            console.log(error)
          })
    }
  }
  ,
  mounted(){
    this.$watch('$app.userId', this.handleUserIdChange);
    this.$http.get(`${this.$app.url}/get_medicine?olderId=${this.$app.userId}`).then(
        response => {
          this.result.medicineData = response.data
          console.log(response.data)
        },
        err => {
          console.log(err)
        }
    );
  }
}
</script>

<style scoped>
span.sorted{
  opacity: 0.3;
  user-select: none;
}

span.sorted_reverse{
  margin-right: 10px;
  opacity: 0.3;
  user-select: none;
}

span.active{
  opacity: 1;
}

div.sort{
  cursor: pointer;
}
</style>
