<template>
    <div style="margin-top: 20px; margin-left: 30px; margin-right: 30px">
      <div class="card shadow mb-4 col-lg-12">
        <div class="card-header py-3 d-flex flex-row align-items-center justify-content-between">
          <h2 class="m-0 font-weight-bold text-primary">医生信息展示</h2>
          <button @click="toggleEdit" align="right" class="btn btn-sm block text-primary background-color-2">{{ editing ? '保存' : '编辑' }}</button>
        </div>
        <div class="container-fluid" style="font-size: 20px;">
            <div class="row">
                <div class="col-md-6">
                    <div class="info-group card-body">
                        <!-- 上传照片的部分 -->
                        <label>照片：</label>
                        <label v-if="editing" for="photoUpload" class="btn btn-primary">上传照片</label>
                        <input type="file" id="photoUpload" @change="uploadPhoto" accept="image/*" style="display: none">
                        <div v-if="!doctor.photo" style="width: 400px; height: 400px; border-style: solid; display: flex; justify-content: center; align-items: center;">
                            <span>未上传照片</span>
                        </div>
                        <div v-else style="width: 400px; height: 400px; border-style: solid; display: flex; justify-content: center; align-items: center;">
                            <img style="width: 400px; height: 400px;" :src="doctor.photo" class="img-thumbnail" alt="医生照片">
                        </div>
                    </div>
                </div>
                <div class="col-md-3">
                    <!-- 医生信息编辑部分 -->
                    <div class="info-group card-body">
                        <label>姓名：</label>
                        <span v-if="!editing">{{ doctor.name }}</span>
                        <input type="text" v-model="doctor.name" v-if="editing" class="form-control">
                    </div>

                    <div class="info-group card-body">
                        <label>性别：</label>
                        <span v-if="!editing">{{ doctor.gender ? "女" : "男"}}</span>
                        <select :v-model="doctor.gender ? '女' : '男' " v-if="editing" class="form-control">
                        <option value="男">男</option>
                        <option value="女">女</option>
                        </select>
                    </div>

                    <div class="info-group card-body">
                        <label>年龄：</label>
                        <span v-if="!editing">{{ doctor.age }}</span>
                        <input type="number" v-model="doctor.age" v-if="editing" class="form-control">
                    </div>

                    <div class="info-group card-body">
                        <label>手机号：</label>
                        <span v-if="!editing">{{ doctor.phone }}</span>
                        <input type="tel" v-model="doctor.phone" v-if="editing" class="form-control">
                    </div>

                    <div class="info-group card-body">
                        <label>医院：</label>
                        <span v-if="!editing">{{ doctor.belong }}</span>
                        <input type="text" v-model="doctor.belong" v-if="editing" class="form-control">
                    </div>

                    <div class="info-group card-body">
                        <label>职位：</label>
                        <span v-if="!editing">{{ doctor.position }}</span>
                        <input type="text" v-model="doctor.position" v-if="editing" class="form-control">
                    </div>
                </div>
            </div>
        </div>
      </div>
    </div>
  </template>
  
  <script>
import { Loading } from 'element-ui';

  export default {
    name: "DoctorInfo",
    data() {
      return {
        doctor: {
            photo:'',
            name: "",
            gender: "",
            age: '',
            phone: "",
            hospital: "",
            position: "",
            photo_path:""
        },
        editing: false
      };
    },
    methods: {
        toggleEdit() {
        this.editing = !this.editing;
        },
        uploadPhoto(event) {
            const file = event.target.files[0];
            // 在实际应用中，你需要根据具体情况处理文件上传逻辑
            // 这里简化为直接将文件转换为 base64 编码
            const reader = new FileReader();
            reader.onload = () => {
                this.doctor.photo = reader.result;
                console.log(this.doctor.photo);
            };
            reader.readAsDataURL(file);
        }
    },
    mounted(){
    this.$watch('$app.userId', this.handleUserIdChange);
    this.$http.get(`${this.$app.url}/getDoctorInfo?doctorId=${localStorage.getItem("doctorId")}`).then(
        response => {
          this.doctor.age = response.data.doctorInfo.age
          this.doctor.belong = response.data.doctorInfo.belong
          this.doctor.name = response.data.doctorInfo.name
          this.doctor.gender = response.data.doctorInfo.gender
          this.doctor.photo_path = response.data.doctorInfo.photo_path
          this.doctor.phone = response.data.doctorInfo.phone
          this.doctor.position = response.data.doctorInfo.position
          console.log(this.doctor);
          this.$http.get(`${this.$app.url}/getDoctorimage?imgpath=${this.doctor.photo_path}`).then(
            response => {
                this.doctor.photo = "data:image/jpeg;base64," + response.data
            },
            err => {
                console.log(err)
            }
        );
        },
        err => {
          console.log(err)
        }
    );

    }
  };
  </script>
  
  <style scoped>
  .container {
    max-width: 600px;
    margin: 0 auto;
  }
  
  .info-group {
    margin-bottom: 15px;
  }
  
  label {
    font-weight: bold;
  }
  </style>