<template>
    <div class="card shadow mb-4">
      <div
          class="card-header py-3 d-flex flex-row align-items-center justify-content-between">
        <h6 class="m-0 font-weight-bold text-primary">历史体温</h6>
        <div class="block">
          <el-date-picker v-model="beginDate" align="right" type="date" placeholder="Select Date" :picker-options="pickerOptions">
          </el-date-picker>
          --
          <el-date-picker v-model="endDate" align="right" type="date" placeholder="Select Date" :picker-options="pickerOptions">
          </el-date-picker>
        </div>
      </div>
      <div class="card-body">
        <div class="chart-area">
          <canvas ref="myAreaChart"></canvas>
        </div>
      </div>
    </div>
</template>

<script>
export default {
  name: 'HistoryBodyTemperature',
  data(){
    return{
      result:[],
      selectResult: [],
      pickerOptions: {
        disabledDate: this.disabledDateFunc
      },
      beginDate: '',
      endDate: '',
      myLineChart: ''
    }
  },
  watch: {
    beginDate(newValue, oldValue){
      if(this.beginDate && this.endDate){
        this.selectResult = this.result.filter(item => {
          const itemDate = new Date(item.date);
          const beginDate = new Date(this.beginDate);
          const endDate = new Date(this.endDate);
          return itemDate >= beginDate && itemDate <= new Date(endDate.setDate(endDate.getDate()+1));
        });
      }else if(!this.endDate && !this.beginDate){
        this.selectResult = this.result
      }else if(!this.endDate){
        this.selectResult = this.result
            .filter(item => new Date(item.date) >=  new Date(this.beginDate))
      }else if(!this.beginDate){
        this.selectResult = this.result
            .filter(item => new Date(item.date) <=  new Date(endDate.setDate(endDate.getDate()+1)))
      }

      this.createChart()
    },
    endDate(newValue, oldValue){
      if(this.beginDate && this.endDate){
        this.selectResult = this.result.filter(item => {
          const itemDate = new Date(item.date)
          const beginDate = new Date(this.beginDate);
          const endDate = new Date(this.endDate);
          return itemDate >= beginDate && itemDate <= new Date(endDate.setDate(endDate.getDate()+1));
        });
      }else if(!this.endDate && !this.beginDate){
        this.selectResult = this.result
      }else if(!this.endDate){
        this.selectResult = this.result
            .filter(item => new Date(item.date) >=  new Date(this.beginDate))
      }else if(!this.beginDate){
        const endDate = new Date(this.endDate);
        this.selectResult = this.result
            .filter(item => new Date(item.date) <=  new Date(endDate.setDate(endDate.getDate()+1)))
      }

      this.createChart()
    }
  },
  methods:{
    disabledDateFunc(time){
      const now = new Date();
      return time.getTime() > now.getTime() || time.getTime() < new Date(this.result[0].date).getTime();
    },
    createChart(){
      // Set new default font family and font color to mimic Bootstrap's default styling
      Chart.defaults.global.defaultFontFamily = 'Nunito', '-apple-system,system-ui,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif';
      Chart.defaults.global.defaultFontColor = '#858796';

      if (this.myLineChart) {
        this.myLineChart.destroy();
      }

      function number_format(number, decimals, dec_point, thousands_sep) {
        // *     example: number_format(1234.56, 2, ',', ' ');
        // *     return: '1 234,56'
        number = (number + '').replace(',', '').replace(' ', '');
        var n = !isFinite(+number) ? 0 : +number,
            prec = !isFinite(+decimals) ? 0 : Math.abs(decimals),
            sep = (typeof thousands_sep === 'undefined') ? ',' : thousands_sep,
            dec = (typeof dec_point === 'undefined') ? '.' : dec_point,
            s = '',
            toFixedFix = function(n, prec) {
              var k = Math.pow(10, prec);
              return '' + Math.round(n * k) / k;
            };
        // Fix for IE parseFloat(0.55).toFixed(0) = 0;
        s = (prec ? toFixedFix(n, prec) : '' + Math.round(n)).split('.');
        if (s[0].length > 3) {
          s[0] = s[0].replace(/\B(?=(?:\d{3})+(?!\d))/g, sep);
        }
        if ((s[1] || '').length < prec) {
          s[1] = s[1] || '';
          s[1] += new Array(prec - s[1].length + 1).join('0');
        }
        return s.join(dec);
      }

      // Area Chart Example
      var ctx = this.$refs.myAreaChart
      this.myLineChart = new Chart(ctx, {
        type: 'line',
        data: {
          labels: this.selectResult.map(item => item.date),
          datasets: [{
            label: "最高体温",
            lineTension: 0.3,
            backgroundColor: "rgba(207, 240, 158, 0.05)",
            borderColor: "#3b8686",
            pointRadius: 3,
            pointBackgroundColor: "#79bd9a",
            pointBorderColor: "#79bd9a",
            pointHoverRadius: 5,
            pointHoverBackgroundColor: "#79bd9a",
            pointHoverBorderColor: "#79bd9a",
            pointHitRadius: 10,
            pointBorderWidth: 2,
            data: this.selectResult.map(item => item.maxBodyTemperature),
          }, {
            label: "最低体温",
            lineTension: 0.3,
            backgroundColor: "rgba(207, 240, 158, 0.1)",
            borderColor: "#79bd9a",
            pointRadius: 3,
            pointBackgroundColor: "#a8dba8",
            pointBorderColor: "#a8dba8",
            pointHoverRadius: 5,
            pointHoverBackgroundColor: "#a8dba8",
            pointHoverBorderColor: "#a8dba8",
            pointHitRadius: 10,
            pointBorderWidth: 2,
            data: this.selectResult.map(item => item.minBodyTemperature),
          }],
        },
        options: {
          maintainAspectRatio: false,
          layout: {
            padding: {
              left: 10,
              right: 25,
              top: 25,
              bottom: 0
            }
          },
          scales: {
            xAxes: [{
              time: {
                unit: 'date'
              },
              gridLines: {
                display: false,
                drawBorder: false
              },
              ticks: {
                maxTicksLimit: 7
              }
            }],
            yAxes: [{
              ticks: {
                maxTicksLimit: 5,
                padding: 10,
                // Include a dollar sign in the ticks
                callback: function(value, index, values) {
                  return number_format(value) + ' ℃' ;
                }
              },
              gridLines: {
                color: "rgb(234, 236, 244)",
                zeroLineColor: "rgb(234, 236, 244)",
                drawBorder: false,
                borderDash: [2],
                zeroLineBorderDash: [2]
              }
            }],
          },
          legend: {
            display: true
          },
          tooltips: {
            backgroundColor: "rgb(255,255,255)",
            bodyFontColor: "#858796",
            titleMarginBottom: 10,
            titleFontColor: '#6e707e',
            titleFontSize: 14,
            borderColor: '#dddfeb',
            borderWidth: 1,
            xPadding: 15,
            yPadding: 15,
            displayColors: false,
            intersect: false,
            mode: 'index',
            caretPadding: 10,
            callbacks: {
              label: function(tooltipItem, chart) {
                var datasetLabel = chart.datasets[tooltipItem.datasetIndex].label || '';
                return datasetLabel + ': ' + number_format(tooltipItem.yLabel) + ' ℃';
              }
            }
          }
        }
      });
    }
  },
  mounted() {
    this.$http.get(`${this.$app.url}/get_historyBodyTemperature?userId=${this.$app.userId}`).then(
        response => {
          this.result = response.data
          this.selectResult = this.result
          this.createChart()
        },
        err => {
          console.log(err)
        }
    )
  }
}
</script>

