<template>
  <nav class="navbar navbar-expand navbar-light bg-white topbar mb-4 static-top shadow">
    <!-- <el-dropdown @command="handleCommand">
      <el-button class="bg-primary" type="primary">
        {{ this.$app.userId }}<i class="el-icon-arrow-down el-icon--right"></i>
      </el-button>
      <el-dropdown-menu slot="dropdown">
        <el-dropdown-item
            v-for="(item, index) in users"
            :key="index"
            :command="item">{{ item }}
        </el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown> -->
    <div class="col-lg-1">
      <div class="dropdown">
        <button class="btn btn-primary dropdown-toggle" type="button"
            id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="false"
            aria-expanded="false">
            {{ nowName }}
        </button>
        <div class="dropdown-menu animated--fade-in"
            aria-labelledby="dropdownMenuButton">
            <a class="dropdown-item"
              v-for="(item, index) in olderInfo"
              :key="index"
              @click="handleSelection(item)">{{ item.name }}
            </a>
        </div>
      </div>
    </div>
  <ul class="navbar-nav ml-auto">
    <li class="nav-item dropdown no-arrow">
      <a class="nav-link dropdown-toggle" href="#" id="userDropdown" role="button"
          data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">

        <span class="mr-2 d-none d-lg-inline text-gray-600 small">{{ username }}</span>

        <img class="img-profile rounded-circle"
              src="img/undraw_profile.svg">
      </a>
      <div class="dropdown-menu dropdown-menu-right shadow animated--grow-in"
            aria-labelledby="userDropdown">
        <a class="dropdown-item" href="#" data-toggle="modal" data-target="#logoutModal">
          <i class="fas fa-sign-out-alt fa-sm fa-fw mr-2 text-gray-400"></i>
          Logout
        </a>
      </div>
    </li>
  </ul>
  </nav>
</template>

<script>
import axios from "axios";
export default {

  name: "Header",
  data(){
    return{
      olderInfo:[],
      testData:{
        olderEmail:'234@qq.com',
        relationEmail:'345@qq.com'
      },
      nowName :'请选择照顾对象'
    }
  },
  computed:{
    username(){
      return localStorage.getItem('username')
    }
  },
  methods:{
    handleSelection(item) {
      this.$app.userId = item.olderId
      this.nowName = item.name
    },
    test(){
      axios.post(`${this.$app.url}/bindRelation`, this.testData)
          .then(response => {
            console.log(response.data)
          })
          .catch(error => {
            console.error(error.response.data)
          })
    }
  },
  mounted(){
    this.$http.get(`${this.$app.url}/getAllOlderInfo`).then(
        response => {
          this.olderInfo = response.data
          // console.log(response.data)
        },
        err => {
          console.log(err)
        }
    );
  }

}
</script>

