<template>
    <div id="container" class="container-fluid">
        <div class="card shadow container">
            <div
                id="title" class="card-header py-3 d-flex flex-row align-items-center justify-content-between">
            <h6 class="m-0 linear-gradient-text" style="font-size: 30px;">心率变异性HRV指数: {{ HRV }}</h6>
            </div>  
            <!-- <div id="title">
                <span class="linear-gradient-text" style="font-size: 30px;">心率变异性HRV指数: {{ HRV }}</span>
            </div> -->
            <div class="palette">
                <div :class="{ 'select-color': whichArr[0] , 'color': true, 'no-select-color': !whichArr[0]}"><span class="linear-gradient-text">很糟糕</span></div>
                <div :class="{ 'select-color': whichArr[1] , 'color': true, 'no-select-color': !whichArr[1]}"><span class="linear-gradient-text">有点差</span></div>
                <div :class="{ 'select-color': whichArr[2] , 'color': true, 'no-select-color': !whichArr[2]}"><span class="linear-gradient-text">正常 </span></div>
                <div :class="{ 'select-color': whichArr[3] , 'color': true, 'no-select-color': !whichArr[3]}"><span class="linear-gradient-text">还不错</span></div>
                <div :class="{ 'select-color': whichArr[4] , 'color': true, 'no-select-color': !whichArr[4]}"><span class="linear-gradient-text">好极了</span></div>
            </div>
            <div id="stats">
                <span>HRV (心率变异性) 是指心跳间隔时间的变化程度，通常用来评估自主神经系统的活动状态。较高的 HRV 通常与心脏健康、适应性较强的自主神经系统有关；而较低的 HRV 则可能与心血管疾病、焦虑、抑郁等不良健康状况相关。</span>
                <!-- <svg xmlns="http://www.w3.org/2000/svg" width="18" height="18" viewBox="0 0 18 18">
                <path d="M4 7.5c-.83 0-1.5.67-1.5 1.5s.67 1.5 1.5 1.5S5.5 9.83 5.5 9 4.83 7.5 4 7.5zm10 0c-.83 0-1.5.67-1.5 1.5s.67 1.5 1.5 1.5 1.5-.67 1.5-1.5-.67-1.5-1.5-1.5zm-5 0c-.83 0-1.5.67-1.5 1.5s.67 1.5 1.5 1.5 1.5-.67 1.5-1.5S9.83 7.5 9 7.5z"></path>
                </svg> -->
            </div>
        </div>
    </div>
</template>

<script>
export default{
name: 'HRVPage',
data(){
      return{
        HRV: 40,
        whichArr:[false,false,false,false,false]
      }
    },
    mounted(){
      if(this.HRV < 30){
        this.whichArr = [true,false,false,false,false];
      }else if(this.HRV < 45){
        this.whichArr = [false,true,false,false,false];
      }else if(this.HRV < 65){
        this.whichArr = [false,false,true,false,false];
      }else if(this.HRV < 80){
        this.whichArr = [false,false,false,true,false];
      }else{
        this.whichArr = [false,false,false,false,true];
      }
    }
}
</script>

<style scoped>
