    width: 100%;
    height: 100vh; /* 设置高度为100%屏幕高度 */
}

.container {
  height: 47%;
  overflow: hidden;
  font-family: sans-serif;
  padding: 30px;
  padding-bottom: 0;
  padding-top: 0;
}

.linear-gradient-text {
  background: linear-gradient(43deg, #ffb835 0%, #C850C0 46%, #4158D0 100%);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
}

.palette {
  display: flex;
  height: 86%;
  width: 100%;
}

.color {
  height: 100%;
  flex: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: 600;
  letter-spacing: 1px;
  transition: flex 0.1s linear;  
  font-size: 40px;
}

