  transition: opacity 0.1s linear;
}

.color:nth-child(1) {
  background: linear-gradient(43deg, #D13C6C 0%, #e74a3b 46%, #A24789 100%);
}

.color:nth-child(2) {
  background: linear-gradient(43deg, #FFBE63 0%, #f6c23e 46%, #FFBD8A 100%);
}

.color:nth-child(3) {
  background: linear-gradient(43deg, #A5AAE1 0%, #4E73DF 46%, #766BD1 100%);
}

.color:nth-child(4) {
  background: linear-gradient(43deg, #5EDEB3 0%, #43D3C0 46%, #80E8A2 100%);
}

.color:nth-child(5) {
  background: linear-gradient(43deg, #68EDCB 0%, #61df61 46%, #78ff75 100%);
}

.no-select-color span{
  opacity: 0;
}

.select-color {
  flex: 5;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  opacity: 1;
}

