  box-sizing: border-box;
  color: #3b8686;
}

#stats {
  height: 40%;
  width: 100%;
  background: white;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0;
  box-sizing: border-box;
  color: #3b8686;
}

#stats svg {
  fill: #bebebe;
  transform: scale(1.2);
}
</style>