<template>
  <div class="card shadow mb-4">
    <div
        class="card-header py-3 d-flex flex-row align-items-center justify-content-between">
      <h6 class="m-0 font-weight-bold text-primary">今日温度</h6>
    </div>
    <div class="card-body">
      <div class="chart-area">
        <canvas ref="temChart"></canvas>
      </div>
    </div>
  </div>
</template>

<script>
import moment from "moment/moment";
import axios from "axios";

export default {
  name: "TodayTemperature",
  data(){
    return{
      result:[],
      info:{
        date:''
      },
      temChart:'',
    }
  },
  methods:{
    createTemChart(){
      // Set new default font family and font color to mimic Bootstrap's default styling
      Chart.defaults.global.defaultFontFamily = 'Nunito', '-apple-system,system-ui,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif';
      Chart.defaults.global.defaultFontColor = '#858796';

      function number_format(number, decimals, dec_point, thousands_sep) {

        number = (number + '').replace(',', '').replace(' ', '');
        let n = !isFinite(+number) ? 0 : +number,
            prec = !isFinite(+decimals) ? 0 : Math.abs(decimals),
            sep = (typeof thousands_sep === 'undefined') ? ',' : thousands_sep,
            dec = (typeof dec_point === 'undefined') ? '.' : dec_point,
            s = '',
            toFixedFix = function(n, prec) {
              let k = Math.pow(10, prec);
              return '' + Math.round(n * k) / k;
            };
        // Fix for IE parseFloat(0.55).toFixed(0) = 0;
        s = (prec ? toFixedFix(n, prec) : '' + Math.round(n)).split('.');
        if (s[0].length > 3) {
          s[0] = s[0].replace(/\B(?=(?:\d{3})+(?!\d))/g, sep);
        }
        if ((s[1] || '').length < prec) {
          s[1] = s[1] || '';
          s[1] += new Array(prec - s[1].length + 1).join('0');
        }
        return s.join(dec);
      }

      // Area Chart Example
      let ctx = this.$refs.temChart
      this.temChart = new Chart(ctx, {
        type: 'line',
        data: {
          labels: this.result.map(item => item.time),
          datasets: [{
            label: "Temperature",
            lineTension: 0.3,
            backgroundColor: "rgba(78, 115, 223, 0.05)",
            borderColor: "rgb(77,114,221)",
            pointRadius: 3,
            pointBackgroundColor: "rgb(77,114,221)",
            pointBorderColor: "rgb(77,114,221)",
            pointHoverRadius: 5,
            pointHoverBackgroundColor: "rgb(77,114,221)",
            pointHoverB4D72DDFForderColor: "rgb(77,114,221)",
            pointHitRadius: 10,
            pointBorderWidth: 2,
            data: this.result.map(item => item.temperature),
          }],
        },
        options: {
          maintainAspectRatio: false,
          layout: {
            padding: {
              left: 10,
              right: 25,
              top: 25,
              bottom: 0
            }
          },
          scales: {
            xAxes: [{
              time: {
                unit: 'date'
              },
              gridLines: {
                display: false,
                drawBorder: false
              },
              ticks: {
                maxTicksLimit: 7
              }
            }],
            yAxes: [{
              ticks: {
                maxTicksLimit: 5,
                padding: 10,
                // Include a dollar sign in the ticks
                callback: function(value, index, values) {
                  return number_format(value) + '℃' ;
                }
              },
              gridLines: {
                color: "rgb(234, 236, 244)",
                zeroLineColor: "rgb(234, 236, 244)",
                drawBorder: false,
                borderDash: [2],
                zeroLineBorderDash: [2]
              }
            }],
          },
          legend: {
            display: false
          },
          tooltips: {
            backgroundColor: "rgb(255,255,255)",
            bodyFontColor: "#858796",
            titleMarginBottom: 10,
            titleFontColor: '#6e707e',
            titleFontSize: 14,
            borderColor: '#dddfeb',
            borderWidth: 1,
            xPadding: 15,
            yPadding: 15,
            displayColors: false,
            intersect: false,
            mode: 'index',
            caretPadding: 10,
            callbacks: {
              label: function(tooltipItem, chart) {
                let datasetLabel = chart.datasets[tooltipItem.datasetIndex].label || '';
                return datasetLabel + ': ' + number_format(tooltipItem.yLabel) + ' ℃';
              }
            }
          }
        }
      });
    },

  },
  mounted() {
    let currentDate = moment();
    this.info.date = currentDate.format('YYYY-MM-DD');
    this.info.date = "2024-03-27";

    axios.post(`${this.$app.url}/get_today`, this.info).then(
        response => {
          this.result = response.data
          this.createTemChart()
        },
        err => {
          console.log(err)
        }
    )
  }
}
</script>

