<template>
  <div class="card shadow">
    <div class="card-header border-bottom">
        <h2 class="h5 fw-normal mb-0"><span class="card-collapse-link text-dark d-block" data-bs-toggle="collapse"  aria-expanded="true">待处理消息</span></h2>
    </div>
    <div class="card-body-0">
      <div class="collapse show" id="feeds-box" role="tabpanel">
        <div v-for="item in result.allCallData" :key="item.id" class="p-3 border-bottom border-gray-200">
          <div class="d-flex justify-content-between">
            <div class="d-flex">
              <div class="ms-3">
                <h5 class="fw-normal text-primary">{{ item.olderName }}</h5>
                <p class="mb-0 text-xs text-gray-600 lh-1">有一个未处理的呼叫</p><small class="text-gray-600 fw-light">{{ item.date }} {{ item.time }}</small>
              </div>
            </div>
            <div class="text-end"><small class="text-primary">{{ item.isHandled ? "已处理" : "未处理" }}</small></div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'Message',
  data(){
    return{
      result:{
        callData:[],
        allCallData:[]
      },
      time: "",
    }
  },
  methods:{
    handleUserIdChange(){
      this.$http.get(`${this.$app.url}/get_callData?olderId=${this.$app.userId}&doctorId=${localStorage.getItem("doctorId")}`).then(
          response => {
            this.result.callData = response.data
            this.result.callData = this.result.callData.filter(item => item.isHandled === false);
          },
          err => {
            console.log(err)
          }
      )

      this.$http.get(`${this.$app.url}/get_allCallData?doctorId=${localStorage.getItem("doctorId")}`).then(
          response => {
            this.result.allCallData = response.data
            this.result.allCallData = this.result.allCallData.filter(item => item.isHandled === false);
          },
          err => {
            console.log(err)
          }
      )
    }
  },
  mounted() {
    this.$watch('$app.userId', this.handleUserIdChange);
    this.$http.get(`${this.$app.url}/get_callData?olderId=${this.$app.userId}&doctorId=${localStorage.getItem("doctorId")}`).then(
          response => {
            this.result.callData = response.data
            this.result.callData = this.result.callData.filter(item => item.isHandled === false);
          },
          err => {
            console.log(err)
          }
      )

      this.$http.get(`${this.$app.url}/get_allCallData?doctorId=${localStorage.getItem("doctorId")}`).then(
          response => {
            this.result.allCallData = response.data
            this.result.allCallData = this.result.allCallData.filter(item => item.isHandled === false);
          },
          err => {
            console.log(err)
          }
      )
  }
}
</script>

<style>
a{
  text-decoration: none;
}
</style>