<template>
  <div class="row">
    <div class="col-xl-3 col-md-6 mb-4">
      <div class="card border-left-primary shadow h-100 py-2">
        <div class="card-body">
          <div class="row no-gutters align-items-center">
            <div class="col mr-2">
              <div class="text-lg font-weight-bold text-red text-uppercase mb-1">
                心率</div>
              <span class="h5 mb-0 font-weight-bold text-red">{{ result.heartRate.heartRate }}</span>
              <span class="h5 mb-0 font-weight-bold text-gray-800"> 次/分钟</span>
            </div>
            <div class="col-auto">
              <i class="fas fa-heartbeat fa-2x text-red"></i>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="col-xl-3 col-md-6 mb-4">
      <div class="card border-left-primary shadow h-100 py-2">
        <div class="card-body">
          <div class="row no-gutters align-items-center">
            <div class="col mr-2">
              <div class="text-lg font-weight-bold text-blue text-uppercase mb-1">
                血氧</div>
              <span class="h5 mb-0 font-weight-bold text-blue">{{ result.bloodOxygen.bloodOxygen }}</span>
              <span class="h5 mb-0 font-weight-bold text-gray-800"> %</span>
            </div>
            <div class="col-auto">
              <i class="fas fa-heart fa-2x text-blue"></i>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="col-xl-3 col-md-6 mb-4">
      <div class="card border-left-primary shadow h-100 py-2">
        <div class="card-body">
          <div class="row no-gutters align-items-center">
            <div class="col mr-2">
              <div class="text-lg font-weight-bold text-green text-uppercase mb-1">体温
              </div>
              <div class="row no-gutters align-items-center">
                <div class="col-auto">
                  <span class="h5 mb-0 font-weight-bold text-green">{{ result.bodyTemperature.bodyTemperature }}</span>
                  <span class="h5 mb-0 font-weight-bold text-green"> ℃</span>
                </div>
              </div>
            </div>
            <div class="col-auto">
              <i class="fas fa-temperature-high fa-2x text-green"></i>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="col-xl-3 col-md-6 mb-4">
      <div class="card border-left-primary shadow h-100 py-2">
        <div class="card-body">
          <div class="row no-gutters align-items-center">
            <div class="col mr-2">
              <div class="text-lg font-weight-bold text-primary text-uppercase mb-1">
                时间</div>
              <span class="h5 mb-0 font-weight-bold text-primary">{{ time }}</span>
            </div>
            <div class="col-auto">
              <i class="fas fa-clock fa-2x text-primary"></i>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "InfoCard",
  data() {
    return {
      result: {
        heartRate: {},
        bloodOxygen: {},
        bodyTemperature: {}
      },
      time: "",
    }
  },
  methods:{
    handleUserIdChange(){
      this.$http.get(`${this.$app.url}/get_todayHeartRate?userId=${this.$app.userId}`).then(
          response => {
            this.result.heartRate = response.data
          },
          err => {
            console.log(err)
          }
      )
      this.$http.get(`${this.$app.url}/get_todayBloodOxygen?userId=${this.$app.userId}`).then(
          response => {
            this.result.bloodOxygen = response.data
          },
          err => {
            console.log(err)
          }
      )
      this.$http.get(`${this.$app.url}/get_todayBodyTemperature?userId=${this.$app.userId}`).then(
          response => {
            this.result.bodyTemperature = response.data
          },
          err => {
            console.log(err)
          }
      )
    }
  },
  mounted() {
    this.$watch('$app.userId', this.handleUserIdChange)

    this.$http.get(`${this.$app.url}/get_todayHeartRate?userId=${this.$app.userId}`).then(
        response => {
          this.result.heartRate = response.data
        },
        err => {
          console.log(err)
        }
    )
    this.$http.get(`${this.$app.url}/get_todayBloodOxygen?userId=${this.$app.userId}`).then(
        response => {
          this.result.bloodOxygen = response.data
        },
        err => {
          console.log(err)
        }
    )
    this.$http.get(`${this.$app.url}/get_todayBodyTemperature?userId=${this.$app.userId}`).then(
        response => {
          this.result.bodyTemperature = response.data
        },
        err => {
          console.log(err)
        }
    )

    // 每秒更新一次时间
    setInterval(() => {
      this.time = new Date().toLocaleTimeString();
    }, 1000);
  }
}
</script>

<style scoped>

</style>
