<template>
  <div class="container-fluid">
  <InfoCard></InfoCard>
    <div class="row">
      <div class="col-lg-6 mb-4">
        <div class="card shadow mb-4">
          <div class="card-header py-3">
            <h6 class="m-0 font-weight-bold text-primary">呼叫比例</h6>
          </div>
          <div class="card-body">
            <h4 class="small font-weight-bold text-danger">紧急 <span
                class="float-right">{{ result.dangerPercent}}%</span></h4>
            <div class="progress mb-4">
              <div class="progress-bar bg-danger" role="progressbar" :style="{width: result.dangerWidth}"
                   :aria-valuenow="result.dangerPercent" aria-valuemin="0" aria-valuemax="100"></div>
            </div>
            <h4 class="small font-weight-bold text-warning">一般<span
                class="float-right">{{ result.warningPercent}}%</span></h4>
            <div class="progress mb-4">
              <div class="progress-bar bg-warning" role="progressbar" :style="{width: result.warningWidth}"
                   :aria-valuenow="result.warningPercent" aria-valuemin="0" aria-valuemax="100"></div>
            </div>
          </div>
        </div>

        <!-- Color System -->
        <div class="row">
          <div class="col-lg-6 mb-4">
            <div class="card bg-warning text-white shadow">
              <div class="card-body">
                紧急
              </div>
            </div>
          </div>
          <div class="col-lg-6 mb-4">
            <div class="card bg-danger text-white shadow">
              <div class="card-body">
                一般
              </div>
            </div>
          </div>
        </div>
        <TodaySmoke></TodaySmoke>
      </div>
      <div class="col-lg-6 mb-4">

        <!-- Illustrations -->
        <div class="card shadow mb-4">
          <!-- <div class="card-header py-3">
            <h6 class="m-0 font-weight-bold text-primary">实时视频</h6>
          </div>
          <div class="card-body">
            <h3>
              <img class="monitor" ref="video" src="${this.$app.url}/monitorVideo" width="100%" height="100%" alt="加载失败">
            </h3>
          </div> -->
          <Map></Map>
        </div>

      </div>
    </div>
    <div class="card shadow mb-4 col-lg-12">
      <div class="card-header py-3 d-flex flex-row align-items-center justify-content-between">
        <h6 class="m-0 font-weight-bold text-primary">历史呼叫</h6>
        <!-- <a class="btn btn-success btn-circle" >
          <i class="fas fa-check"></i>
        </a> -->
      </div>
      <div class="card-body">
        <div class="table-responsive">
          <table class="table table-bordered" id="dataTable" width="100%" cellspacing="0">
            <thead>
            <tr>
              <th>
                <div class="row">
                  <div class="col mr-2">呼叫编号</div>
                  <div class="sort" @click="sort(0,'id')" ref="id">
                    <span class="sorted">↑</span>
                    <span class="sorted_reverse">↓</span>
                  </div>
                </div>
              </th>
              <th>
                <div class="row">
                  <div class="col mr-2">姓名</div>
                  <div class="sort" @click="sort(1,'name')" ref="name">
                    <span class="sorted">↑</span>
                    <span class="sorted_reverse">↓</span>
                  </div>
                </div>
              </th>
              <th>
                <div class="row">
                  <div class="col mr-2">呼叫信息</div>
                  <div class="sort" @click="sort(2,'content')" ref="content">
                    <span class="sorted ">↑</span>
                    <span class="sorted_reverse">↓</span>
                  </div>
                </div>
              </th>
              
              <th class="sorted">
                <div class="row">
                  <div class="col mr-2">日期</div>
                  <div class="sort" @click="sort(3,'date')" ref="date">
                    <span class="sorted ">↑</span>
                    <span class="sorted_reverse">↓</span>
                  </div>
                </div>
              </th>
              <th class="sorted">
                <div class="row">
                  <div class="col mr-2">时间</div>
                  <div class="sort" @click="sort(4,'time')" ref="time">
                    <span class="sorted ">↑</span>
                    <span class="sorted_reverse">↓</span>
                  </div>
                </div>
              </th>
              <th class="sorted">
                <div class="row">
                  <div class="col mr-2">是否处理</div>
                  <div class="sort" @click="sort(5,'isHandled')" ref="isHandled">
                    <span class="sorted">↑</span>
                    <span class="sorted_reverse">↓</span>
                  </div>
                </div>
              </th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="item in result.sortedCallData" :key="item.id">
              <td class="col-lg-1">{{ item.id }}</td>
              <td class="col-lg-1">{{ item.olderId }}</td>
              <td class="col-lg-4">{{ item.content }}</td>
              <td class="col-lg-2">{{ item.date }}</td>
              <td class="col-lg-2">{{ item.time }}</td>
              <td align="center" class="col-lg-2">
                <a @click="handleCallMessage(item)" :class="['btn', 'btn-icon-split',
                {
                  'btn-success' : item.isHandled,
                  'btn-danger' : !item.isHandled & item.callType == 1,
                  'btn-warning' : !item.isHandled & item.callType == 2
                }]" style="margin-right: 10px;">
                  <span class="icon text-white-50">
                      <i class="fas fa-check"></i>
                  </span>
                  <span class="text">{{ item.isHandled ? '已处理' : '处理' }}</span>
                </a>
                <a class="btn btn-icon-split btn-primary" data-toggle="modal" data-target="#logoutModal" @click="getNowHandling(item.id)">
                  <span class="icon text-white-50">
                      <i class="fas fa-check"></i>
                  </span>
                  <span class="text">回复</span>
                </a>
              </td>

            </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>

    <div class="modal fade" id="logoutModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel"
        aria-hidden="true">
        <div class="modal-dialog" role="document">
            <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title" id="exampleModalLabel">回复</h5>
                    <button class="close" type="button" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true">×</span>
                    </button>
                </div>
                <div class="modal-body">                
                  <el-input
                    type="textarea"
                    :rows="4"
                    placeholder="请输入内容"
                    v-model="textarea">
                  </el-input></div>
                <div class="modal-footer">
                    <button class="btn btn-secondary" type="button" data-dismiss="modal" @click="cleanTextArea">取消</button>
                    <a class="btn btn-primary" data-dismiss="modal" @click="giveMessage">回复</a>
                </div>
            </div>
        </div>
    </div>

  </div>
  
</template>

<script>
import axios from "axios";
import TodaySmoke from "@/components/TodaySmoke.vue";
import InfoCard from "@/components/InfoCard.vue";
import Map from "@/components/Map.vue"

export default {
  name: "Monitor",
  components: {InfoCard, TodaySmoke, Map},
  data(){
    return{
      result:{
        callData:[],
        sortedCallData:[],
        sortedStatus:[true,true,true,true,true,true],
        warningPercent: 0,
        dangerPercent: 0,
        warningWidth: '0%',
        dangerWidth: '0%',
      },
      time: "",
      textarea: '',
      nowHandlingCall: ''
    }
  },
  mounted() {
    this.$watch('$app.userId', this.handleUserIdChange)
    this.getCallData()

    // 每秒更新一次时间
    setInterval(() => {
      this.time = new Date().toLocaleTimeString();
    }, 1000);

  },
  methods: {
    cleanTextArea(){
      this.textarea = ''
    },
    getNowHandling(id){
      this.nowHandlingCall = this.result.callData.find(item => item.id === id);
      this.textarea = this.nowHandlingCall.doctorReply
    },
    handleCallMessage(item){
      item.isHandled = !item.isHandled
      axios.post(`${this.$app.url}/handleCallMessage`, item)
          .then(response =>{
            this.getCallData()
          })
          .catch(error => {
            console.log(error)
          })
    },
    giveMessage(){
      this.nowHandlingCall.doctorReply = this.textarea;
      axios.post("${this.$app.url}/set_callData", this.nowHandlingCall)
          .then(response =>{
            this.getCallData()
          })
          .catch(error => {
            console.log(error)
          })
    },
    sort(index, type){
      if(this.result.sortedStatus[index]){
        this.result.sortedCallData = this.result.callData.sort((a, b) => {
          return a[type] > b[type] ? 1 : -1;
        })
      }else {
        this.result.sortedCallData = this.result.callData.sort((a, b) => {
          return b[type] > a[type] ? 1 : -1;
        })
      }

      this.result.sortedStatus[index] = !this.result.sortedStatus[index]

      const activeElements =  document.getElementsByClassName('active')
      for(let i = 0; i < activeElements.length; i++) {
        activeElements[i].classList.remove("active")
      }

      if(!this.result.sortedStatus[index]){
        this.$refs[type].querySelector(":first-child").classList.add("active")
      }else {
        this.$refs[type].querySelector(":last-child").classList.add("active")
      }
    },
    getCallData(){
      this.$http.get(`${this.$app.url}/get_callData?olderId=${this.$app.userId}&doctorId=${localStorage.getItem("doctorId")}`).then(
          response => {
            this.result.callData = response.data
            this.result.sortedCallData = response.data
            let notHandledCallData = this.result.callData.filter(item => item.isHandled === false);

            if(notHandledCallData.length === 0){
              this.result.dangerPercent = 0;
              this.result.warningPercent = 0;
              this.result.dangerWidth = '0%'
              this.result.warningWidth = '0%'
              return
            }


            this.result.dangerPercent = (notHandledCallData.filter(item => item.callType === 1).length / notHandledCallData.length * 100).toFixed(0);
            this.result.warningPercent = (notHandledCallData.filter(item => item.callType === 2).length / notHandledCallData.length * 100).toFixed(0);
            this.result.dangerWidth = this.result.dangerPercent + '%'
            this.result.warningWidth = this.result.warningPercent + '%'
          },
          err => {
            console.log(err)
          }
      )
    },
    handleUserIdChange(){
      this.getCallData()
    },
  }
}
</script>

<style scoped>
span.sorted{
  opacity: 0.3;
  user-select: none;
}

span.sorted_reverse{
  margin-right: 10px;
  opacity: 0.3;
  user-select: none;
}

span.active{
  opacity: 1;
}

div.sort{
  cursor: pointer;
}

</style>
