<template>
  <div class="container" style="top: 250px;position: relative;">
    <div class="row justify-content-center">
      <div class="col-xl-12 col-lg-12 col-md-9">
        <div class="card o-hidden border-0 shadow-lg my-5">
          <div class="card-body p-0">
            <div class="row">
              <div class="col-lg-6 d-none d-lg-block">
                <img alt="" src="../assets/img/LOGO.png" width="100%" height="100%">
              </div>
              <div class="col-lg-6">
                <div class="p-5">
                  <div class="text-center">
                    <h1 class="h4 text-gray-900 mb-4">欢 迎 !</h1>
                  </div>
                  <form class="user" @submit.prevent="login">
                    <div class="form-group">
                      <input type="email" class="form-control form-control-user"
                             id="exampleInputEmail" aria-describedby="emailHelp"
                             placeholder="请输入邮箱..." v-model="userData.email">
                    </div>
                    <div class="form-group">
                      <input type="password" class="form-control form-control-user"
                             id="exampleInputPassword" placeholder="请输入密码..." v-model="userData.password">
                    </div>
                    <div class="form-group">
                      <div class="custom-control custom-checkbox small">
                        <input type="checkbox" class="custom-control-input" id="customCheck">
                        <label class="custom-control-label" for="customCheck">记住我</label>
                      </div>
                    </div>
                    <button type="submit" class="btn btn-primary btn-user btn-block">登录</button>
                    <hr>
                  </form>
                  <hr>
                  <div class="text-center">
                    <router-link to="/forgot-password" class="small" href="forgot-password.html">忘记密码?</router-link>
                  </div>
                  <div class="text-center">
                    <router-link to="/register" class="small" href="register.html">创建账户</router-link>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: 'Login',
  data() {
    return {
      userData:{
        email: '',
        password: ''
      }
    };
  },
  methods: {
    //登录
    login() {
      axios.post(`${this.$app.url}/login`, this.userData)
          .then(response => {
            localStorage.setItem('isLoggedIn', 'true');
            localStorage.setItem('email', this.userData.email);
            localStorage.setItem('username', response.data.username);
            localStorage.setItem('doctorId', response.data.userId);
            console.log(response)
            this.$router.push({name:"section"},()=>{});
          })
          .catch(error => {
            if(error.response.status === 401){
              alert("密码错误！")
            }
            else if(error.response.status === 404){
              alert("账户不存在！")
            }
          })
    },
  },
}
</script>

<style scoped>

</style>
